@font-face {
    font-family: myFont;
    src: url(/fonts/3979.ttf);
}

@font-face {
    font-family: Thin;
    src: url(/fonts/StMarie-Thin.ttf);
}

@font-face {
    font-family: Ubuntu;
    src: url(/fonts/3976.ttf);
}

@font-face {
    font-family: Memorandum;
    src: url(/fonts/15304.ttf);
}

* {
    padding: 0;
    margin: 0 auto;
}

.header {
    /*position: fixed;
    background-color: white;*/
    width: 1260px;
    height: 122px;
    padding-top: 3px;
}

.logo {
    float: left;
    width: 88px;
    height: 106px;
    background-image: url(/img/rabbit1.jpg);
}

.span1 {
    float: left;
    width: 200px;
    padding-top: 12px;
    font-weight: bold;
}

.orange {
    position: relative;
    font-size: 35px;
    color: orange;
    left: 59px;
}

.rabbit {
    font-size: 37px;
    position: relative;
    left: 55px;
    font-family: myFont;
    color: rgb(71, 71, 71)
}

.top-menu {
    float: right;
    padding-top: 14px;
}

.home, .about, .work, .blog, .contact {
    float: left;
    font-size: 23px;
    height: 47px;
    width: 55px;
    text-align: center;
    margin-right: 75px;
}

a {
    display: block;
    text-decoration: none;
    color: grey;
}
/*end_header*/

.border_slider {
    /*margin-top: 122px;
    overflow-x: visible;*/
    background-image: url(/img/slider.jpg);
    background-position-x: center;
    background-repeat: no-repeat;
}

.slider {
    left: 50%;
    height: 410px;
    width: 1260px;
}

.span2 {
    width: 410px;
    position: relative;
    top: 144px;
    right: 425px;
    font-family: myFont;
    font-size: 38px;
    font-weight: bold;
}

.span3 {
    position: relative;
    color: orange;
    left: 5px;
    bottom: 3px;;
    padding: 9px 15px 9px 30px;
    background-color: rgb(71, 71, 71);
}

.span4 {
    position: relative;
    color: rgb(71, 71, 71);
    background-color: rgb(233, 162, 30); 
    top: 43px;
    padding: 11px 20px 15px 32px;
}
/*end_slider*/

.main {
    margin: 0 auto;
    max-width: 1260px;
    font-family: Ubuntu;
}

.content1 {
    padding: 28px 57px 0px 8px;
    display: flex;
    flex-wrap: wrap; 
    font-size: 14px;
    font-weight: bold; 
    line-height: 165%;
}

.item1, .item2, .item3, .item4 {
    width: 25%;
    
}

.item5, .item6, .item7, .item8 {
    text-align: right;
    color: orange;
    width: 17%;
} 
/*end content1*/

.content2 {
    padding-top: 40px; 
}

.span5, .span6 {
    text-align: center;
    font-size: 45px;
    text-decoration: underline;
    color: orange;
    font-family: Thin; 
}

.card {
    display: flex;
    padding-top: 37px;
    justify-content: space-between;
}

.card_span1, .card_span2, .card_span3 {
    text-align: center;
    font-size: 20px;
}

.cardSpan {
    color: orange;
}

.img_card {
    padding: 22px 0;
}

.text {
    font-size: 14px;
    line-height: 158%;
    padding-left: 40px;
    font-weight: bold;
}
/*end content2*/

.content3 {
    padding-top: 39px;
}

form {
    font-size: 25px;
    color: orange;
    padding-top: 25px;
}

table {
    border-spacing: 0px 36px;
    width: 100%;
}

input[type=text], input[type=email] {    
    height: 30px;
    width: 1064px;  
    background-color: #f8f7f6;
    border: 2px solid #d2d0cf;
    border-left: 5px solid #d2d0cf; 
    font-size: 25px;    
}
.message {
    vertical-align: text-top;
}

textarea {
    font-size: 25px;
    height: 85px;
    width: 1064px;  
    background-color: #f8f7f6;
    border: 2px solid #d2d0cf;
    border-left: 5px solid #d2d0cf;
}

input[type=submit] {
    font-size: 35px;
    height: 48px;
    width: 177px;
    color: white;
    background: orange;
    font-family: Thin;
}

.submit {
    padding-left: 860px;
}

/*end forms*/

.footer {
    color: white;
    background-color: orange;
    font-size: 35px;
    height: 60px;
    font-family: Thin;
    text-align: center;
    padding-top: 12px;
}